.overlay-text {
  height: 100%;
  letter-spacing: .03em;
  color: #fff;
  background-color: rgba(33, 150, 243, .9);
  padding: 63px 24px;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.56;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  bottom: 0;
  overflow: auto;
  transform: translateY(101%);
}

.project-element:hover .overlay-text {
  opacity: 1;
  transform: translateY(0);
}

.backdrop {
  content: "";
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .2);
  transition: opacity .25s linear, visibility .25s linear;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.backdrop.is-hidden .modal {
  transform: translate(-50%, -50%)scaleY(0);
}

.modal {
  min-width: 90%;
  min-height: 60%;
  background-color: var(--primary-wite-color);
  border-radius: 4px;
  padding: 40px;
  transition: transform .25s linear;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scaleY(1);
}

@media screen and (min-width: 480px) {
  .modal {
    min-width: 450px;
    min-height: 609px;
  }
}

@media screen and (min-width: 1200px) {
  .modal {
    min-width: 528px;
    min-height: 581px;
  }
}

.modal-close {
  cursor: pointer;
  background-color: var(--primary-wite-color);
  color: rgba(0, 0, 0, .1);
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.icon-close {
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
}

.modal-close:hover .icon-close {
